<script>
import { gsap, Sine, Circ, Bounce } from "gsap";

const roundTheEye = (eye, eyeLentil) => {
    gsap.to(eye, {
            scale: 2.5,
            duration: .5,
            ease: Bounce.easeIn,
            transformOrigin: "center",
            repeat: 0
    });
    gsap.to(eyeLentil, {
            rotate: 360,
            duration: .3,
            ease: Circ.easeInOut,
            transformOrigin: "center right",
            repeat: 2,
            yoyo: true
    });    
}

const normalizeTheEye = (target) => {
    gsap.to(target, {
            scale: 1,
            duration: 1,
            ease: Sine.easeOut,
            repeat: 0
    });   
}

const waveFin = (target, duration = 2) => {
  let anim = gsap.to(target, {
    x: 8,
    duration,
    ease: Sine.easeInOut,
    yoyo: true,
    repeat: -1
  });
  return anim;
}

const sideMoveFin = (target, duration = 1) => {
  let anim = gsap.to(target, {
    scaleX: .8,
    duration,
    transformOrigin:"center left",
    ease: Circ.easeIn,
    yoyo: true,
    repeat: -1
  });
  return anim;
}

const rotateFin = (target, duration = 2) => {
  let anim =  gsap.to(target, {
    rotate: 25,
    duration,
    delay: .5,
    transformOrigin:"center left",
    ease: Sine.easeInOut,
    yoyo: true,
    repeat: -1
  });
  return anim;
}

const moveInTank = (target) => {
    const duration = "random([15, 20])";
    let tl = gsap.timeline({repeat: -1});
    tl.fromTo(target, {
        right: "0%",
        x: "100%"
    }, {
        duration,
        ease: "power1.inOut",
        right: "100%",
        x: "0%"
    }).set(target, {scaleX: -1})
        .to(target, {
        duration,
        ease: "power1.inOut",
        right: "0%",
        x: "100%"
     });
     return tl;
}    

import FloatMove from "@/components/FloatMove.vue";

export default {
  props: {
    size: {
      type: String,
      default: "xs",
    },
    fishColor: {
        type: String,
        default: "#e3ae00"
    },
    stressLevel: {
        type: Number,
        default: 1
    }
  },
  data() {
      return {
          animations: {},
          defaultSpeeds: {
              waving: 2,

          }
      }
  },
  components: {
    FloatMove
  },
  watch: {
      stressLevel(newVal) {
          /* Handle Eyes */
          if(newVal === 2) {
            roundTheEye(this.$refs.eye, this.$refs.eyelentil);
          } else if(newVal === 1) {
            normalizeTheEye(this.$refs.eye)
          }

          /* Handle Speed */    
          let timeScale = (newVal > 1) ? newVal * 2.5 : 1;
          for (const [animation] of Object.entries(this.animations)) {
            this.animations[animation].timeScale(timeScale);
          }        
      }
  },
  mounted() {
    this.animations.backfin = waveFin(this.$refs.backfin);
    this.animations.pelvinfin = waveFin(this.$refs.pelvinfin);
    this.animations.pectoralfin = rotateFin(this.$refs.pectoralfin);
    this.animations.tailfin = sideMoveFin(this.$refs.tailfin);
    this.animations.moveInTank = moveInTank(this.$refs.fish);
  },
};
</script>

<style scoped lang="scss">
.fish-wrapper {
  position: absolute;
  top: 35%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}
.fish {
  position: absolute; 
  max-width: 100%;
  height: auto;
}
.fish--xs {
  width: 80px;
  @media screen and (min-width: 640px) {
    width: 100px;
  }  
}
.fish--md {
  width: 100px;
  @media screen and (min-width: 640px) {
    width: 140px;
  } 
}
.fish--lg {
  width: 240px;
  @media screen and (min-width: 640px) {
    width: 300px;
  } 
}
</style>